//
// Variables
//

$button-transition: all 0.25s easeInOutCubic !default;
$button-font-weight: $global-font-weight-bold !default;
$button-focus-box-shadow: 0 0 0 1px #fff, 0 0 2px 2px $global-primary !default;

$button-background: #ececec !default;
$button-background-hover: darken($button-background, 5) !default;

$button-space-enable: true !default;
$button-space-width: 260px !default;
$button-small-enable: true !default;
$button-mini-enable: true !default;

$button-font-size: $global-font-size !default;
$button-font-size-small: $global-font-size-small !default;
$button-font-size-mini: $global-font-size-small !default;

$button-primary-enable: true !default;
$button-primary-background: $global-primary !default;
$button-primary-background-hover: $global-primary-hover !default;

$button-secondary-enable: true !default;
$button-secondary-background: $global-secondary !default;
$button-secondary-background-hover: $global-secondary-hover !default;

$button-secondary-enable: true !default;
$button-secondary-background: $global-secondary !default;
$button-secondary-background-hover: $global-secondary-hover !default;

$button-danger-enable: true !default;
$button-danger-background: $global-danger !default;
$button-danger-background-hover: $global-danger-hover !default;

$button-outline-enable: true !default;
$button-contrast-enable: true !default;
$button-icon-enable: true !default;

//
// Component
//

@mixin Button() {

  // 1. Correct inability to style clickable `input` types in iOS.
  // 2. Remove margins in Chrome, Safari and Opera.
  // 3. Normalizes styles for `button`.
  // 4. Address `overflow` set to `hidden` in IE 8/9/10/11.
  // 5. Correct `font` properties and `color` not being inherited for `button`.
  // 6. Address inconsistent `text-transform` inheritance which is only inherit in Firefox and IE
  // 7. Style
  // 8. `line-height` is used to create a height
  // 9. `min-height` is necessary for `input` elements in Firefox and Opera because `line-height` is not working.
  // 10. Reset button group whitespace hack
  // 11. Required for `a`.
  // 12. Do not wrap buttons
  // 13. Removes inner padding and border in Firefox 4+.
  .Button {
    // 1
    appearance: none;
    // 2
    margin: 0;
    // 3
    border: 0;
    // 4
    overflow: visible;
    // 5
    font: inherit;
    font-weight: $button-font-weight;
    color: $global-font-color;
    // 6
    text-transform: none;
    // 7
    display: inline-block;
    padding: 0 $global-whitespace--large;
    background: $button-background;
    vertical-align: middle;
    cursor: pointer;
    border-radius: $global-border-radius;
    transition: $button-transition;
    // 8
    line-height: $global-height;
    // 9
    height: $global-height;
    // 10
    font-size: $button-font-size;
    // 11
    text-decoration: none;
    text-align: center;
    // 12
    white-space: nowrap;

    // 13
    &::-moz-focus-inner {
      border: 0;
      padding: 0;
    }

    &:hover,
    &:focus,
    &.is-active {
      color: $global-font-color;
      background: $button-background-hover;
    }

    @if $button-contrast-enable {
      .u-contrast & {
        &:not(.Button--noContrast) {
          background: rgba($global-contrast, 0.15);
          color: $global-contrast;
          border: 1px solid $global-contrast;

          &:hover,
          &:focus,
          &.is-active {
            background: rgba($global-contrast, 0.25);
            color: $global-contrast;
          }
        }
      }
    }

    @if $button-space-enable {
      &--space {
        max-width: 100%;
        width: $button-space-width;
      }
    }

    @if $button-small-enable {
      &--small {
        font-size: $button-font-size-small;
        line-height: $global-height-small;
        height: $global-height-small;
        padding: 0 $global-whitespace--medium;
        @include hook-button-small();
      }
    }

    @if $button-mini-enable {
      &--mini {
        font-size: $button-font-size-mini;
        line-height: $global-height-mini;
        height: $global-height-mini;
        padding: 0 $global-whitespace--regular;
        @include hook-button-mini();
      }
    }

    @if $button-outline-enable {
      &--outline {
        background: transparent;
        border: 1px solid $global-primary;
        color: $global-primary;
        position: relative;
        z-index: 2;

        &:hover,
        &:focus,
        &.is-active {
          color: $global-primary-hover;
          background: transparent;
        }

        @if $button-contrast-enable {
          .u-contrast & {
            &:not(.Button--noContrast) {
              color: $global-contrast;
              border-color: $global-contrast;
            }
          }
        }

        @include hook-button-outline();
      }
    }

    @if $button-primary-enable {
      &--primary {
        color: $global-contrast;
        background: $button-primary-background;

        &:hover,
        &:focus,
        &.is-active {
          background: $button-primary-background-hover;
          color: rgba($global-contrast, 0.9);
        }

        @if $button-contrast-enable {
          .u-contrast & {
            &:not(.Button--noContrast) {
              background: $global-contrast;
              color: $global-primary;

              &:hover,
              &:focus,
              &.is-active {
                background: rgba($global-contrast, 0.9);
                border-color: rgba($global-contrast, 0.9);
                color: $global-primary-hover;
              }
            }
          }
        }

        @include hook-button-primary();
      }
    }

    @if $button-secondary-enable {
      &--secondary {
        color: $global-contrast;
        background: $button-secondary-background;

        &:hover,
        &:focus,
        &.is-active {
          background: $button-secondary-background-hover;
          color: rgba($global-contrast, 0.9);
        }

        @if $button-contrast-enable {
          .u-contrast & {
            &:not(.Button--noContrast) {
              background: $global-contrast;
              color: $global-secondary;

              &:hover,
              &:focus,
              &.is-active {
                background: rgba($global-contrast, 0.9);
                border-color: rgba($global-contrast, 0.9);
                color: $global-secondary-hover;
              }
            }
          }
        }

        @include hook-button-secondary();
      }
    }

    @if $button-danger-enable {
      &--danger {
        color: $global-contrast;
        background: $button-danger-background;

        &:hover,
        &:focus,
        &.is-active {
          background: $button-danger-background-hover;
          color: rgba($global-contrast, 0.9);
        }

        @if $button-contrast-enable {
          .u-contrast & {
            &:not(.Button--noContrast) {
              background: $global-contrast;
              color: $global-danger;

              &:hover,
              &:focus,
              &.is-active {
                background: rgba($global-contrast, 0.9);
                border-color: rgba($global-contrast, 0.9);
                color: $global-danger-hover;
              }
            }
          }
        }

        @include hook-button-danger();
      }
    }

    @if $button-icon-enable {
      &--withIcon {
        svg {
          margin-right: $global-whitespace--small;
        }

        &Right {
          svg {
            margin-right: 0;
            margin-left: $global-whitespace--small;
          }
        }

        @include hook-button-with-icon();
      }
    }

    &--fullWidth {
      width: 100%;
    }

    // 1. Replicate default button styles
    // 2. Styles
    &.is-disabled,
    &[disabled] {
      // 1
      color: $global-font-color;
      background: $button-background;
      // 2
      opacity: $global-disabled-opacity;
      cursor: not-allowed;

      @if $button-contrast-enable {
        .u-contrast & {
          &:not(.Button--noContrast) {
            background: transparent;
            border: 1px solid $global-contrast;
            color: $global-contrast;

            &::after,
            &::before {
              display: none;
            }
          }
        }
      }
    }

    @include hook-button();
  }
}
