//
// Component
//

@mixin Flex() {
  .u-flex {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;

    // Child elements automatically match height; this extends that ability to first child element
    &DeepMatch {
      > * {
        display: flex;
        flex-wrap: wrap;

        > * {
          flex: none;
          width: 100%;
        }
      }
    }

    // No Flex: 0 0 auto
    // Content dimensions
    &ItemNone {
      flex: none;
    }

    // Relative Flex: 1 1 auto
    // Space is allocated considering content
    // 1. Fixes flex-shrink value in IE10
    &ItemAuto {
      flex: auto;
      // 1
      -ms-flex-negative: 1;
    }

    // Absolute Flex: 1 1 0%
    // Space is allocated solely based on flex
    &Item1 {
      flex: 1;
    }

    //
    // Alignment modifiers
    //

    // Cross-start margin edge of the items is placed on the cross-start line
    &Start {
      justify-content: start !important;
    }

    // Items are centered along the line
    &Center {
      justify-content: center !important;
    }

    // Forces row to right
    &End {
      justify-content: flex-end !important;
    }

    // Cross-start margin edge of the items is placed on the cross-start line
    &Top {
      align-items: flex-start !important;
    }

    // Items are centered in the cross-axis
    &Middle {
      align-items: center !important;
    }

    // Cross-end margin edge of the items is placed on the cross-end line
    &Bottom {
      align-items: flex-end !important;
    }

    @media (min-width: $global-viewport--small) {
      &Start--small {
        justify-content: start !important;
      }

      &Center--small {
        justify-content: center !important;
      }

      &End--small {
        justify-content: flex-end !important;
      }

      &Top--small {
        align-items: flex-start !important;
      }

      &Middle--small {
        align-items: center !important;
      }

      &Bottom--small {
        align-items: flex-end !important;
      }
    }

    @media (min-width: $global-viewport--medium) {
      &Start--medium {
        justify-content: start !important;
      }

      &Center--medium {
        justify-content: center !important;
      }

      &End--medium {
        justify-content: flex-end !important;
      }

      &Top--medium {
        align-items: flex-start !important;
      }

      &Middle--medium {
        align-items: center !important;
      }

      &Bottom--medium {
        align-items: flex-end !important;
      }
    }

    @media (min-width: $global-viewport--large) {
      &Start--large {
        justify-content: start !important;
      }

      &Center--large {
        justify-content: center !important;
      }

      &End--large {
        justify-content: flex-end !important;
      }

      &Top--large {
        align-items: flex-start !important;
      }

      &Middle--large {
        align-items: center !important;
      }

      &Bottom--large {
        align-items: flex-end !important;
      }
    }

    @include hook-utility-flex();
  }
}
