//
// Variables
//

$section-vertical-padding: ($global-whitespace--xlarge + $global-whitespace--large) !default;
$section-primary-background: $global-primary !default;
$section-hero-background: $global-primary !default;
$section-hero-min-height: 500px !default;

//
// Component
//

@mixin Section() {
  .Section {
    padding: $section-vertical-padding 0;

    // Used for pages without much content that need to stretch to fill the screen
    &--expandMiddle {
      @media (min-width: $global-viewport--medium) {
        min-height: $global-height-expand;
        display: flex;
        align-items: center;

        > * {
          width: 100%;
          flex: none;
        }
      }

      @include hook-section-expand();
    }

    &--borderTop {
      border-top: 1px solid $global-border;
      @include hook-section-border();
    }

    * {
      &:last-child {
        margin-bottom: 0;
      }
    }

    &-content {
      position: relative;
      z-index: 2;
      @include hook-section-content();
    }

    &--panel {
      background: $global-panel-background;
      @include hook-section-panel();
    }

    &--primary {
      background: $section-primary-background;
      @include hook-section-primary();
    }

    &--hero {
      background: $section-hero-background;
      padding-top: $global-whitespace--xlarge;
      padding-bottom: $global-whitespace--xlarge;
      min-height: $section-hero-min-height;
      display: flex;
      align-items: center;

      > * {
        width: 100%;
      }
    }

    @include hook-section();
  }
}
