//
// Variables
//

$container-max-width: 1100px !default;
$container-small-max-width: 800px !default;

//
// Component
//

@mixin Container() {
  .Container {
    max-width: $container-max-width;
    margin: 0 auto;
    padding: 0 $global-whitespace--regular;

    &--small {
      max-width: $container-small-max-width;
    }

    [class^='Container'] {
      padding: 0;
    }

    @include hook-container();
  }
}
