//
// Component
//

@mixin MiscUtility() {

  // Overflow container
  // Provides a horizontal scrollbar whenever the elements inside it are wider than the container itself. This comes in handy when having to handle tables on a responsive website; which would look illegible compressed.
  .u-overflowContainer {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }

  .u-overflowHidden {
    overflow: hidden;
  }

  // Inline Block
  .u-inlineBlock {
    display: inline-block !important;
  }

  // Block
  .u-block {
    display: block !important;
  }

  @include hook-utility-misc();
}
