//
// Variables
//

$inline-nav-font-size: $global-font-size !default;

//
// Component
//

@mixin Inline() {
  .Inline {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: (-$global-whitespace--regular) 0 0 (-$global-whitespace--regular);

    > * {
      margin-left: $global-whitespace--regular;
      margin-top: $global-whitespace--regular;
    }

    &--center {
      justify-content: center;
    }

    &--right {
      justify-content: flex-end;
    }

    &--small {
      margin-top: (-$global-whitespace--small);
      margin-left: (-$global-whitespace--small);

      > * {
        margin-left: $global-whitespace--small;
        margin-top: $global-whitespace--small;
      }
    }

    &--medium {
      margin-top: (-$global-whitespace--medium);
      margin-left: (-$global-whitespace--medium);

      > * {
        margin-left: $global-whitespace--medium;
        margin-top: $global-whitespace--medium;
      }
    }

    &--large {
      margin-top: (-$global-whitespace--large);
      margin-left: (-$global-whitespace--large);

      > * {
        margin-left: $global-whitespace--large;
        margin-top: $global-whitespace--large;
      }
    }

    &-nav {
      a {
        color: $global-muted;
        white-space: nowrap;
        font-size: $inline-nav-font-size;
        display: inline-block;

        &:hover,
        &:focus {
          color: $global-font-color;
        }
      }

      .is-active {
        a {
          color: $global-primary;
        }
      }

      @include hook-inline-nav();
    }

    @include hook-inline();
  }
}
