//
// Variables
//

$icon-size: 1em !default;
$icon-size-small: 0.65em !default;
$icon-size-large: 1.5em !default;
$icon-size-xlarge: 2em !default;
$icon-size-standalone: 50px !default;

//
// Component
//

@mixin Icon() {
  .Icon {
    fill: currentColor;
    size: $icon-size;
    line-height: 1;
    position: relative;
    top: -1px;
    max-width: 100%;

    a & {
      pointer-events: none;
    }

    &--small {
      size: $icon-size-small;
    }

    &--large {
      size: $icon-size-large;
    }

    &--xlarge {
      size: $icon-size-xlarge;
    }

    // Used for img; not sprites
    &--standalone {
      width: 100%;
      max-width: $icon-size-standalone;
      height: $icon-size-standalone;
    }

    @include hook-icon();
  }
}
